section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1440px;
}

.toTheLeftSideBox {
  display: flex;
  justify-content: flex-start;
  width: 100%;

  h2 {
    text-shadow: 4px 3px 20px rgba(0, 0, 0, 0.38);
    font-size: clamp(1rem, 1vw + 0.75rem, 1.75rem);
    font-weight: 700;
    letter-spacing: -0.07rem;
  }
}

.mainProjectBox {
  border-radius: 5px;
  background-color: rgb(117, 118, 115);
  width: 100%;
  max-width: 1440px;
  min-height: 300px;
  max-height: 460px;

  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;

  padding: 0.5rem 0.5rem;
  gap: 0.5rem;

  img {
    flex: 1;
    max-height: 100%;
    height: 100%;
    object-fit: cover;
    width: calc(50% - 10px);
    border-radius: 5px;
  }

  div {
    flex: 1;
    max-height: 100%;
    height: 100%;
    width: calc(50% - 10px);

    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;

    background: transparent;
    pointer-events: none;
    visibility: hidden;
  }
}

